___CSS_LOADER_EXPORT___.push([module.id, `@keyframes tab-ripple {
    to {
        transform: scale(4);
        opacity: 0;
    }
}

@keyframes tab-slide-in {
    from {
        transform: translateX(-100%);
        opacity: 0;
    }

    to {
        transform: translateX(0%);
        opacity: 1;
    }

    /* from {
		transform: scale(0);
		opacity: 0;
	}
	to {
		transform: scale(1);
		opacity: 1;	
	} */
}

span.tab-ripple {
    position: absolute;
    /* The absolute position we mentioned earlier */
    border-radius: 50%;
    transform: scale(0);
    animation: tab-ripple 400ms linear;
    background-color: rgba(255, 255, 255, 0.5);
}

span.tab-ripple-vertical {
    animation: tab-ripple 400ms linear;
    /* For vertical tabs, we might want a different expansion behavior */
    transform-origin: center center;
}

span.tab-ripple-horizontal {
    animation: tab-ripple 400ms linear;
    transform-origin: center center;
}

.my-tabstrip-ul {
    padding: 0;
    margin: 0;
    list-style-type: none;
    height: 100%;
    display: flex;
    flex-wrap: nowrap;
    align-items: stretch;
    line-height: initial;
    text-align: initial;
}

.my-tabstrip-ul * {
    box-sizing: border-box;
}

.my-tabstrip-ul-horizontal {
    width: 100%;
    flex-direction: row;
}

.my-tabstrip-ul-vertical {
    width: 100%;
    flex-direction: column;
}


.my-tabstrip-li {
    cursor: pointer;
    display: flex;
    align-items: center;
    min-width: 0;
    /* Serve per far andare overflow ellipsis */
    animation: tab-slide-in 0.3s linear;
    position: relative;
}

.my-tabstrip-li-wrapper {
    position: relative;
    overflow: hidden;
    width: 100%;
    height: 100%;
}

.my-tabstrip-li:hover {
    filter: brightness(1.05);
}

.my-tabstrip-li-content {
    display: flex;
    flex-direction: column;
    flex: 1;
    min-width: 0;
}


.my-tabstrip-li-subtitle {
    font-size: 10px;
    /* color: rgba(0, 0, 0, 0.6); */
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    margin-bottom: 2px;
    min-width: 0;
}

.my-tabstrip-li-a {
    flex: 1;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    min-width: 0;
}

.my-tabstrip-li-icon {
    padding: 2px 4px;
    margin-left: 8px;
    margin-right: -8px;
}

.my-tabstrip-li-icon:hover {
    filter: brightness(1.05);
}

/* Compact close icon badge styling */
.my-tabstrip-li-close-icon-compact {
    position: absolute;
    border-radius: 50% !important;
    width: 20px !important;
    height: 20px !important;
    display: flex !important;
    align-items: center !important;
    justify-content: center !important;
    font-size: 12px !important;
    padding: 2px 4px !important;
    box-sizing: border-box !important;
    cursor: pointer !important;
}

.active:hover {
    filter: none !important;
}

.action-add {
    width: initial !important;
    border: 0;
    align-items: center;
    cursor: pointer;
}

.action-add a {
    font-size: 20px;
    height: 24px;
    line-height: 16px;
    padding: 2px;
    border-radius: 4px;
}

/* Vertical add button styling */
.my-tabstrip-ul-vertical .action-add {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    padding: 8px 16px;
}

.my-tabstrip-add-label {
    margin-left: 8px;
    white-space: nowrap;
}

/* Compact vertical tab styles */
.my-tabstrip-li-compact {
    justify-content: center;
}

.my-tabstrip-li-content-compact {
    width: 100%;
    height: 100%;
    align-items: center;
    justify-content: center;
}

.my-tabstrip-li-a-compact {
    text-align: center;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}




